//data layer
//reducer 
//reducer = how the product data is dispatch to data layer when add to cart button is clicked

export const initialState = {
    basket: [],
    user: null,
}
//SELECTOR
export const getBasketTotalAmout = (basket) => basket?.reduce((amount, item) => item.price + amount, 0)

const reducer = (state, action) => {
    console.log(action);
    switch(action.type){
        case 'ADD_TO_BASKET':
            return {
                ...state,
                basket: [...state.basket, action.item]
            };

        case 'EMPTY_BASKET':
            return {
                ...state,
                basket: []
            };

        case 'REMOVE_FROM_BASKET':
                const index = state.basket.findIndex((basketItem) => basketItem.id === action.id); 
                let newBasket = [...state.basket];

                if (index => 0){
                    newBasket.splice(index, 1);
                }
                else{
                    console.warn(`Cant remove the product (id: ${action.id}) as its not on the basket.`)
                }

                return{
                    ...state,
                    basket: newBasket
                }

        case 'SET_USER':
                return{
                    ...state,
                    user: action.user

                }
        default:
            return state;
    }
};

export default reducer;